body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding-top: 32px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  display: flex;
}

.card__img {
  width: 160px;
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  width: 166px;
  margin: 0;
  font-size: 1.2em;
  font-weight: 500;
  line-height: 18px;
}

.card__description {
  color: #616070;
  width: 166px;
  margin-bottom: 16px;
  font-size: 1em;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin-bottom: 24px;
  display: flex;
}

.card__rating-text {
  color: #060b35;
  margin: 0;
  font-size: 1em;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  width: 166px;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-size: 1.2em;
  line-height: 18px;
}

.card__price-value {
  margin: 0;
  font-size: 1.6em;
  font-weight: 500;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
  font-size: 1.4em;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background: none;
  transition: all .3s;
}

.stars {
  width: 159.36px px;
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--4 > :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
